<!-- iview 3.5.3 -->
<template>
    <div>
        <section class="main">
            <div class="logo text-center">
                <h1><a href="#"> Welcome Sign In </a></h1>
            </div>
            <div class="content-w3ls text-center">
                <img src="../assets/img/admin.png" alt="" class="img-responsive">
                <Form :model="formItem" label-position="right" :label-width="100" ref="formValidate"
                      :rules="ruleValidate">
                    <FormItem label="" prop="userName" class="wthree-field" :label-width="10">
                        <Input v-model="formItem.userName" placeholder="Username"></Input>
                    </FormItem>
                    <FormItem label="" prop="password" class="wthree-field" :label-width="10">
                        <Input v-model="formItem.password" type="password" placeholder="Password"></Input>
                    </FormItem>
                    <FormItem label="" prop="" class="wthree-field" :label-width="10">
                        <Button type="primary" class="btn" @click="submit('formValidate')">Sign In</Button>
                    </FormItem>
                    <div class="login-bottom">
                        <a href="#url" class="">Create An Account</a>
                    </div>
                </Form>
            </div>
            <div class="copyright">
                <p>© 2020 Universe Signin Form. Made with love | Designed by <a
                        href="https://w3layouts.com" target="_blank">W3layouts</a></p>
            </div>
        </section>
    </div>
</template>
<script>
    import Home from '@/views/Home'
    import _apiitems from '@/api/items'
    import storage from 'good-storage'

    export default {
        data() {
            return {
                formItem: {
                    userName: "",
                    password: ""
                },
                ruleValidate: {
                    userName: [
                        {required: true, message: " ", trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: " ", trigger: 'blur'}
                    ],
                }
            }
        },
        components: {
            Home
        },
        methods: {
            submit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        _apiitems.login({...this.formItem}).then(res => {
                            if (res) {
                                storage.set("token", res.data);
                                this.$router.push('/home')
                            }
                        }).catch(err => {
                            this.$Notice.error({title: '登录失败!'})
                        })
                    } else {
                        this.$Notice.error({title: '有数据未填写!'})
                    }
                })
            }
        }
    }
</script>
